body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}

.App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@font-face {
    font-family: "EllenFont";
    src: url(/static/media/myfont-regular-webfont.f121731b.eot);
    src: url(/static/media/myfont-regular-webfont.d60af048.woff) format("woff"),
        url(/static/media/Myfont-Regular.b9c25239.otf) format("opentype"),
        url(/static/media/myfont-regular-webfont.1f26879a.svg) format("svg");
}

#header {
    margin: 0px;
    margin-bottom: 0px;
}

#intro {
    height: 88vh;
    background-color: #9fe0dc;
    margin-top: 0px;
}

#headshot {
    height: 90%;
    display: block;
    position: relative;
    top: 10%;
    left: 22%;
    overflow: hidden;
}

#caption {
    font-family: "EllenFont";
    position: absolute;
    right: 13%;
    top: 40%;
    padding: 1em;
    background-color: white;
    border-radius: 5px;
    margin: auto;
    box-shadow: 5px 10px#02C8A7;
    max-width: 30%;
}

@media only screen and (max-width: 960px) {
    #headshot {
        max-height: 90%;
        object-fit: cover;
        bottom: 0;
        /* left: 5%;
        right: 5%; */
        left: 0;
        right: 0;
    }

    #caption {
        min-width: 80%;
        left: 10%;
        bottom: 5%;
        top: 70%;
        /* overflow: scroll; */
    }

    #caption h3 {
        font-size: 200%;
    }

    #caption h5 {
        font-size: 120%;
    }
}

#about {
    height: 400px;
    background-color: #eaf2f8;
}

#experience {
    background-color: #283747;
    height: 400px;
}
#projects {
    font-family: "Space Mono", monospace;
    background-color: #f53240;
    /* min-height: 520px; */
    padding: 20px;
}

#title {
    font-family: "Space Mono", monospace;
    /* color: #1c2833; */
    color: white;
    padding: 10px;
    margin: 0;
}

@media only screen and (min-width: 960px) {
    #title {
        margin: 0 0 0 10%;
    }
}

#contact {
    /* background-color: #1a5276; */
    background-color: #f9be02;
    height: 200px;
}

